<template>
  <div class="character-select-panel">
    <header>Выбор пользователя</header>
    <div class="character-list">
      <div
        class="character"
        v-for="(item, index) in charactersList"
        :key="index"
        @click="selectCharacter(item.name)"
      >
        <SkinFaceViewer
          class="bordered face"
          skin-link="https://www.minecraftskins.com/uploads/skins/2022/08/16/skeleton-fella-chill-20732795.png?v527"
        />
        <div class="name">{{ item.display_name }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import SkinFaceViewer from "@/components/SkinFaceViewer";
import { createRequest } from "@/api/requestBuilder";
import requestConfigs from "@/api/requestConfigs";
export default {
  name: "CharacterSelectPanel",
  components: { SkinFaceViewer },
  data() {
    return {
      charactersList: [],
    };
  },
  beforeMount() {
    createRequest(requestConfigs.GETCharacterList).then((result) => {
      this.charactersList = result.data;
    });
  },
  methods: {
    selectCharacter(name) {
      this.$store.commit("dumbMutation", {
        field: "selectedCharacterName",
        value: name,
      });
    },
  },
};
</script>

<style lang="scss">
.character-select-panel {
  flex: 2;

  header {
    font-family: "Press Start 2P", cursive;
    color: white;
    font-size: 20px;
    margin-bottom: 15px;
  }

  .character-list {
    padding: 20px;
    display: flex;
    gap: 15px;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }

  .character {
    display: flex;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
    width: 100px;
    height: 100px;

    &:hover {
      transform: translateY(-10px);
    }

    .face {
      margin-bottom: 15px;
      flex-shrink: 0;
    }
    .name {
      color: white;
      font-family: "Press Start 2P", cursive;
      font-size: 13px;
    }
  }
}
</style>
